.delete {

    /* margin-left: 30px; */
    min-width: 50px;
    border: 2px solid darkblue;
    border-radius: 3px;
    background-color: rgb(133, 133, 211);
    opacity: 0.8;
}

.delete:active {
    box-shadow: 0 2px #666;
}

.delete:hover,
.delete:focus {
    opacity: 1;
    color: azure;
    font-weight: 600;
    transform: scale(1.1);
}